const NewDoubtModal = ({ open, handleClose }) => {
  const [title, setTitle] = useState("");
  const [desc, setDesc] = useState("");
  const [images, setImages] = useState([]);

  const handleChangeImage = (e) => {
    const image = e.target.files[0];
    const reader = new FileReader();
    if (image) {
      try {
        reader.readAsDataURL(image);
      } catch (error) {
        console.log(error);
      }
      reader.onloadend = () => {
        if (reader.result)
          setImages([
            ...images,
            {
              title: image.name,
              src: reader.result,
            },
          ]);
      };
    }
  };

  const handleClickDelete = (src) => {
    setImages(images.filter((item) => item.src !== src));
  };

  const handleSubmit = () => {};

  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      style={{ zIndex: 999999 }}
      className={styles.dialogContainer}
    >
      <DialogTitle id="alert-dialog-title">{"Ask a doubt"}</DialogTitle>
      <DialogContent>
        <form onSubmit={handleSubmit} className={styles.dialogContentContainer}>
          <div
            className={classNames(
              styles.modalInputContainer,
              styles.flexColumn
            )}
          >
            <label htmlFor="title">Question</label>
            <input
              name="title"
              value={title}
              type="text"
              onChange={(e) => setTitle(e.target.value)}
            />
          </div>
          <div
            className={classNames(
              styles.modalInputContainer,
              styles.flexColumn
            )}
          >
            <label htmlFor="desc">Description</label>
            <textarea
              name="desc"
              value={desc}
              type="text"
              minLength="6"
              onChange={(e) => setDesc(e.target.value)}
            />
          </div>
          <div className={styles.addImagesContainer}>
            {images.map((image) => {
              return (
                <div className={styles.imgContainer}>
                  <img src={image.src} alt="question" />
                  <IconButton
                    onClick={() => handleClickDelete(image.src)}
                    className={styles.deleteBtn}
                  >
                    <HighlightOffIcon fontSize="small" />
                  </IconButton>
                </div>
              );
            })}
            {/* <Button className={styles.addImagesBtn} variant="contained">
              <AddPhotoAlternateIcon />
            </Button> */}
            <Button
              className={styles.addImagesBtn}
              variant="contained"
              component="label"
              onClick={() => {
                if (images.length === 3) {
                  alert("Can't add more than 3 images");
                }
              }}
            >
              <AddPhotoAlternateIcon />
              {images.length <= 2 && (
                <input
                  required
                  type="file"
                  id="file"
                  name="image"
                  onChange={handleChangeImage}
                  accept="image/jpg, image/png, image/jpeg"
                  style={{ width: "0", opacity: "0" }}
                />
              )}
            </Button>
          </div>
        </form>
      </DialogContent>
      <DialogActions>
        <Tooltip
          className={styles.tooltip}
          placement="top"
          title="Live Doubt Discussion"
        >
          <IconButton className={styles.liveDisBtn}>
            <VideocamIcon fontSize="small" />
          </IconButton>
        </Tooltip>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
        <Button onClick={handleClose} color="primary" autoFocus>
          Submit
        </Button>
      </DialogActions>
    </Dialog>
  );
};
